export default class PermutationLister {
  private static _permutations: string[][] = [];
  private static _specifiedCount: number = -1;

  public static generatePermutations(
    items: string[], 
    specifiedCount: number = PermutationLister._specifiedCount
  ): string[][] {
    PermutationLister._permutations.length = 0;
    PermutationLister._specifiedCount = specifiedCount;
    if (PermutationLister._specifiedCount < 0) {
      PermutationLister._specifiedCount = items.length;
    }
    if (0 < PermutationLister._specifiedCount) {
      let partialItems: string[] = [];
      this.generatePermutationsRecursively(items, partialItems);
    }
    return PermutationLister._permutations;
  }

  private static generatePermutationsRecursively(items: string[], partialItems: string[]): void {
    let storedPartialItems: string[] = [];
    for (let i: number = 0; i < items.length; i++) {
      storedPartialItems = partialItems.concat(items[i]);
      if (storedPartialItems.length < PermutationLister._specifiedCount) {
        let slicedItems: string[] = [];
        // Different from class "CombinationCalc"
        slicedItems = slicedItems.concat(items.slice(0, i), items.slice(i + 1));
        this.generatePermutationsRecursively(slicedItems, storedPartialItems);
      } else {
        if (!PermutationLister._permutations.some((permutation) => {
          if (JSON.stringify(permutation) === JSON.stringify(storedPartialItems)) {
            return permutation;
          }
        })) {
          PermutationLister._permutations.splice(PermutationLister._permutations.length, 0, storedPartialItems);
        }
      }
    }
  }
}